import HttpRepository from "./Repository/HttpRepository";
import SecureObjectFactory from "./Factory/SecureObjectFactory";
import QueryFactory from "./Factory/QueryFactory";

/**
 * Entry point for the Apisearch client
 *
 * @param apiKey
 * @param endpoint
 *
 * @returns {Apisearch}
 */
module.exports = function(apiKey, endpoint) {
    if (typeof apiKey === 'undefined') {
       throw new TypeError(`ApiKey parameter must be defined.`)
    }

    return new Apisearch(
        'apisearch-app-id',
        apiKey,
        endpoint
    );
};

/**
 * Apisearch class
 */
class Apisearch {
    constructor(appId, apiKey, endpoint) {
        this.appId = appId;
        this.apiKey = apiKey;
        this.endpoint = endpoint || 'http://127.0.0.1:9002/app.php';

        this.cache = {};
        this.query = QueryFactory;
        this.createObject = SecureObjectFactory;
    }

    search(query, callback) {
        let repository = new HttpRepository(
            this.endpoint,
            this.apiKey
        );

        return repository
            .query(query)
            .then(
                response => callback(response, null)
            )
            .catch(
                error => callback(null, error)
            )
        ;
    }
}


